@media only screen and (max-width: 1200px) {
    :root {
        --default-padding: 30px !important;
    }

    #intro {
        --intro-margin-right: 30px !important;
    }

    #apresentacao {
        padding: 30px var(--default-padding) !important;
    }

    #contatos {
        padding: auto 0px !important;
    }

    .contatos_cards {
        flex-flow: row wrap !important;
        justify-content: center !important;
        align-items: center !important;
    }

    .complement {
        height: 600px !important;
    }
    

}



@media only screen and (max-width: 1000px) {
    #ap-texto {
        width: 350px !important;
    }

    .contatos_container {
        flex-flow: row wrap !important;
        justify-content: center !important;
        align-items: center !important;
    }
}

@media only screen and (max-width: 900px) {

    #apresentacao {
        gap: 25px !important;
    }

    #nossa-missao{
        

        gap: 100px !important;
    }

    #ap-texto {
        font-size: medium !important;
    }


}


@media only screen and (max-width: 800px) {

    #header {
        background-color: red;
    }

    #header_menu {
        display: flex !important;
    }

    #redes-sociais-header {
        display: none !important;
    }

    #navbar {
        --border-lightgray: 1px solid lightgray;
        border-top: var(--border-lightgray) !important;
        border-bottom: var(--border-lightgray) !important;

        width: 100% !important;
        padding: 10px 0 !important;
        position: absolute !important;
        top: 70px !important;
        left: 0 !important;

        flex-flow: column !important;

        background-color: red !important;
        display: none !important;
    }

    #navbar>li {
        width: 100% !important;
    }

    #navbar>li:hover {
        background-color: red !important;
    }

    /* MAIN */
    #main {
        justify-content: center !important;
        align-items: center !important;
        text-align: center !important;
    }


    #intro {
        margin-right: 0px !important;
        align-items: center !important;
    }

    /* APRESENTAÇÂO */
    #apresentacao {
        justify-content: center !important;
    }

    #ap-texto>h1 {
        text-align: center !important;
    }

    #ap-texto>p {
        text-align: center !important;
    }

    #ap-img {
        display: none;
        width: 300px !important;
        height: 400px !important;
    }

    /* NOSSA MISSAO */

    #nossa-missao {
        flex-flow: column !important;
        align-items: center !important;
        gap: 100px !important;
    }


    /* DROPDOWN */

    .dropdown {


        width: 100% !important;
        position: static !important;
        margin-top: 10px !important;


    }

    .dropdown>li {
        width: 100% !important;
        border-bottom: var(--border-lightgray) !important;
    }

    .dropdown>li:hover {
        /* Colocar na mesma cor principal (vermelha) */
        background-color: none !important;
    }

    .dropdown>li>a {
        text-align: center !important;
    }

    .dropdown>li>ion-icon {
        display: none !important;
    }

    /* FOOTER */
    footer {
        padding: 50px 10px !important;
        padding-bottom: 100px !important;
    }

    footer>.content {
        flex-flow: column !important;
        gap: 30px !important;
    }

    .contatos_footer {
        text-align: center !important;
    }

    .contatos_footer>ul>li {
        text-align: center !important;
        justify-content: center !important;
    }

    #redes-sociais-footer {
        justify-content: center !important;
    }

    .footer_titles {
        text-align: center !important;
    }

    .matriz {

        justify-content: center !important;
        align-items: center !important;
        gap: 15px !important;

    }

    .matriz>p {
        text-align: center !important;
    }

}

@media only screen and (max-width: 700px) {

    /* Contatos */
    #contatos {
        justify-content: center !important;
    }

    .contatos_container{
        width: 400px !important;
    }

    .contatos_cards{
        justify-content: center !important;
    }

    #contatos-form{
        justify-content: center !important;
        align-items: center !important;
    }

    .form-div{
        flex-flow: column !important;
        justify-content: center !important;
        align-items: center !important;
    }

    /* Slide */
    .slide-show {
        --slide_min_width: 300px !important;
    }

    .serviceCard {
        width: 400px !important;
        height: 280px !important;
    }

    #nossosServicos>h2 {
        text-align: center !important;
    }

    .complement {
        height: 700px !important;
    }

}

@media only screen and (max-width: 600px) {

    /* NOSSA MISSAO */
    #nossa-missao{
        padding: 0px 10px !important;
        padding-bottom: 100px !important;
    }

    .nm-cards {
        width: 300px !important;
    }

    .nm-cards>h3 {
        font-size: 30px !important;
    }

    /* WHATSAPP */
    #wpp {
        bottom: 12px !important;
        left: 12px !important;
    }
}

@media only screen and (max-width: 500px) {

    :root {
        --button_space: -20px !important;
    }

    .mainTitle {
        font-size: x-large;
    }

    .serviceCard {
        width: 280px !important;
        height: 150px !important;
    }


    .slide-show {
        --slide_min_width: 320px !important;
    }

}

@media only screen and (max-width: 400px) {
    .slide-show {
        --slide_min_width: 250px !important;
    }

    .contatos_cards{
        padding: 0px 5px !important;
    }

    .contatos_container {
        width: 100% !important;
    }
}